//vuex
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';

//
Vue.use(Vuex);

//Store
const store = new Vuex.Store({
    //
    state: {
        topics: [
            { name: 'all',   desc: '', },//selected:true},
            { name: 'good',  desc: '', },//selected:false},
            { name: 'share', desc: '', },//selected:false},
            { name: 'ask',   desc: '', },//selected:false},
            { name: 'job',   desc: '', },//selected:false},
            { name: 'dev',   desc: '', },//selected:false},
        ],
        //
        unLoginedMoreMenus: { login: '' },
        //
        loginedMoreMenus: {create:'', center: '', msg: '', logout: '' },
        //
        isLogined: false,
        //
        loginname:'',
        //accesstoken
        accesstoken:'',
        //ID
        author_id:'',
        //
        isShowMoreMenus: false
    },
    //
    getters: {
        //this.$store.state.
        moreMenus: function (state) {
            if (state.isLogined) {
                //
                return state.loginedMoreMenus;
            } return state.unLoginedMoreMenus;
        },
        getTopicDescByTab: function(state){
            //
            return function(tab){
                for(var index = 0;index < state.topics.length;index++){
                    if(state.topics[index].name == tab){
                        return state.topics[index].desc;
                    }
                }
            };
        }
    },
    //
    mutations,
    //
    actions
});

//
export default store;


