export default {
    name:'center',
    computed: {
        loginname :function(){
            //
            return this.$store.state.loginname;
        }
    },
    created:function(){
        //,
        //pushhistory,
        //replacehistory,,[]
        this.$router.replace('/user/'+this.loginname);
    }
}



